import {AtomicDesign} from '../../../../Frontend/Core/AtomicDesign';
import {getTopToolbarButtons} from '../../../../Frontend/Helpers/toolbar';
import {uuid} from '../../../../Frontend/Helpers/uuid';
// import Strings from '../../../UiResources/strings';

export default class DeployPage extends AtomicDesign {
    
    initToolbar(){
        const toolbarButtons = getTopToolbarButtons("SAVE","FULLSCREEN");
        this.actions().setTopToolbar(toolbarButtons);
    }

    async newEnvironment(){
        try {
            const {value} = await this.vue.$prompt('New environment name', 'New environment', { 
                confirmButtonText: 'OK',
                cancelButtonText: 'Cancel',
            });
            if (value){
                const id = uuid();
                const newEnvironment = {id, name: value, type: "dev", config: {}}
                this.vue.openedDocument.environments.push(newEnvironment);
                this.actions().setOpenedDocument({result:this.vue.openedDocument});
            }

        }catch (e){
            //cancel selected
        }
    }

    async publishEnvironment (){ //({environment}){

        const message = `
        <p>Publishing to an environment is not available in the BETA version</p>
        <br/>
        <p>This feature will be available soon</p>
        `;

        this.vue.$alert(message, 'Feature not available in BETA', {
            dangerouslyUseHTMLString: true
        });
        
        // const {result,error} = await this.backend().publish(environment.id, this.vue.openedDocument.id);

        // if (!error){
        //     this.vue.$notify({type: 'success', message: 'Successfully published', position: 'bottom-right', offset:0 });
        // }else {
        //     this.msgbox(Strings.ERROR_MESSAGES.MESSAGE_ERROR_DEPLOYING, this.MSGBOX_TYPE.OK,Strings.ERROR_MESSAGES.MESSAGE_ERROR_DEPLOYING_TITLE);
        //     const {trace} = result;
        //     console.log(trace);
        // }
    }
}