import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import Strings from '../../../../UiResources/strings';
import {EVENTS} from '../../../../UiResources/events';

export default class EnvironmentCardMolecule extends AtomicDesign {
    async deleteEnvironment(vue,environment){
        const result = await this.msgbox(Strings.MESSAGES.DELETE_ENVIRONMENT, this.MSGBOX_TYPE.YES_NO, Strings.MESSAGES.DELETE_ENVIRONMENT_TITLE);
        if (result == this.MSGBOX_BUTTONS.YES){
            let environments = vue?.openedDocument?.environments;
            if (environments){
                const envIndex = environments.findIndex(env => env.id == environment.id);
                if (envIndex !== -1){
                    environments.splice(envIndex,1);
                    this.actions().setOpenedDocument({result:vue.openedDocument});
                }
            }
        }      
    }

    editConfiguration(vue,setting){
        this.emitGlobalEvent(EVENTS.DIALOG.SHOW_CONFIGURATION,{
            value : {
                id:setting.id,
            },
            field:{
                typeData:{
                    configCategory: setting.type
                }
            }

        });
    }

    publish(vue,environment){
        vue.$emit("publishTriggered",{environment});
    }
}