<template>
  <div class="environment-card-molecule">
    
    <el-dialog :title="'Environment Configuration for : ' + environment.name" :visible.sync="settingsVisible" width="800px" top="60px">

      <div class="environment-card-molecule__dialog lc-custom-scroll">
        <table v-for = "setting of allSettings" :key="setting.id">
          <tr>
            <td colspan="3" class="title">
              <label>{{setting.name}}</label>
              <el-tooltip class="item" effect="dark" content="Edit Configuration" placement="top">
                <img @click="editConfiguration(setting)" src="../../../../../Frontend/public/assets/toolbar/edit-solid.svg"/>
              </el-tooltip>
              
            </td>
          </tr>
          <tr>
            <td class="key">Type</td>
            <td colspan="2">{{setting.type}}</td>
          </tr>
          <tr v-for="(value,key) of setting.config" :key="key" >
            <td class="key">{{key}}</td>
            <td class="value">{{value.value}}</td>
            <td class="value">{{value.inherited ? "Inherited" : ""}}</td>
          </tr>
        </table>
      </div>

      <span slot="footer">
        <el-button type="primary" @click="settingsVisible = false">Close</el-button>
      </span>

    </el-dialog>

    <div class="environment-card-molecule__title">
      <h1>{{environment.name}}</h1>
    </div>
    <div class="environment-card-molecule__config">
      <div class="environment-card-molecule__config__provider">
        <label>Type</label>
        <el-select placeholder="" v-model="environment.type" :filterable="true" :disabled="environment.id == 'dev'" size="small">
            <el-option v-for="(type, typeId) in environmentTypes" :key="typeId" :value="typeId" :label="type.caption"></el-option>
        </el-select>
        <div class="environment-card-molecule__config__provider__toolbar">
    
          <el-tooltip class="item" effect="dark" content="Environment Configuration" placement="bottom">
            <img @click="settingsVisible = true" src="../../../../../Frontend/public/assets/toolbar/cog-solid.svg"/>
          </el-tooltip>

          <el-tooltip class="item" effect="dark" content="Delete environment" placement="bottom">
            <img v-if="environment.id !== 'dev'" @click="deleteEnvironment(environment)" src="../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg"/>
          </el-tooltip>

          <el-tooltip v-if="environmentTypes[environment.type].webhook" class="item" effect="dark" content="Publish via Webhook" placement="bottom">
            <img @click="publish(environment)" src="../../../../../Frontend/public/assets/toolbar/cloud-upload-alt-solid.svg"/>
          </el-tooltip>

          <el-tooltip v-if="environmentTypes[environment.type].download" class="item" effect="dark" content="Download Blueprint" placement="bottom">
            <img @click="publish(environment)" src="../../../../../Frontend/public/assets/toolbar/download-solid.svg"/>
          </el-tooltip>

        </div>
      </div>
      <div class="environment-card-molecule__config__element">
        <table style="width:100%;">
          <tr v-if="environmentTypes[environment.type].debug">
            <td class="label"><label>Debugger Url</label></td>
            <td class="value"><el-input size="small" v-model="environment.config.debugUrl"></el-input></td>
          </tr>
          <tr v-if="environmentTypes[environment.type].webhook">
            <td class="label"><label>Web Hook</label></td>
            <td class="value"><el-input size="small" v-model="environment.config.webhook"></el-input></td>
          </tr>
        </table>   
      </div>
    </div>
  </div>
</template>

<script>
import { ENVIRONMENT_TYPES } from '../../../../../Common/VariousTypes';
import EnvironmentCardMolecule from './EnvironmentCardMolecule.hook'
const hook = new EnvironmentCardMolecule();

export default {
  name: 'EnvironmentCardMolecule',
  beforeCreate() { hook.hook(this)},
  methods: {
    editConfiguration(setting){ hook.editConfiguration(this,setting); },
    deleteEnvironment(environment){ hook.deleteEnvironment(this,environment); },
    publish(environment){ hook.publish(this,environment); }
  },
  computed: {
    allSettings(){
      const supportedConfigs = this.openedDocument.settings.filter(x=>x.type !== "objectConstructor" && x.type !== "run") || [];
      return supportedConfigs.map(x=>{
        const allEnv = x.environments["*"];
        const currentEnv = x.environments[this.environment.id] || {};

        const config = {};
        Object.keys(allEnv).forEach(key=>{
            if (currentEnv[key]){
              config[key] = {
                inherited: false,
                value: currentEnv[key]
              }  
            }else {
              config[key] = {
                inherited: true,
                value: allEnv[key]
              }  
            }
        });

        return {
          id:x.id,
          name:x.name,
          type:x.type,
          config
        }
      }) //.//map(x=> {id: x})
    },
    ...hook.getters("openedDocument")
  },
  props: {
    environment: Object
  },
  data() {
      return {
        settingsVisible: false,
        ENVIRONMENT_TYPES,
        environmentTypes : {
          [ENVIRONMENT_TYPES.DEV_SANDBOX] : {id : ENVIRONMENT_TYPES.DEV_SANDBOX , caption: "Development",debug:true},
          [ENVIRONMENT_TYPES.PRODUCTION]: {id : ENVIRONMENT_TYPES.PRODUCTION , caption: "Production",webhook:true, download:true,debug:true}
        }
      }
  }
}

</script>

<style scoped lang="scss">

.environment-card-molecule {
  margin-bottom:20px;
  height:160px;
  width:100%;
  border-radius: 5px;
  background-color: #333333;

  display: flex;

  &__dialog{
    height:300px;
    overflow: auto;
    background-color: black;
    padding:20px;
    table {
      margin-bottom:30px;
      
      tr {
        td.key {
          padding-bottom:5px;
          width:150px;
        }

        td.value {
          width:150px;
        }

        td.title {
          font-size:18px;
          padding-bottom:20px;

          label {
            width:100%;
          }
          img {
            height:20px;
            cursor: pointer;
            margin-left:20px;

          }
        }
      }
    }
  }

  &__title {
    padding:20px;
    width:200px;
    margin: auto;

    h1 {
      font-weight: 100;
      font-size: 25px;
    }
  }

  &__config {
    padding:20px;
    width: calc(100% - 200px);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    &__provider{
      display: flex;
      flex-direction: row;
      label {
        margin-top:10px;
        margin-right:87px;
        margin-left:3px;
      }

      * > {
        vertical-align: middle;
        
      }

      .el-select {
        width:350px;
      }

      &__toolbar {
        display: inline-block;
        margin-left:20px;
        margin-top:7px;
        
        img {
          height:25px;
          margin-right:15px;
          cursor: pointer;
        }

      }


    }

    &__element {
      height:300px;
      table {
        tr {
          td.label {
            width:150px;
          }

          td.value {
            width:700px;
            .el-input {
              width:350px;
            }
          }
        }

      }

    }
  }
}

</style>
