<template>
  <div class="deployment-page lc-custom-scroll">
    <transition-group name="list">
      <div class="deployment-page__environments" v-for="env of openedDocument.environments" :key="env.id">
          <EnvironmentCardMolecule :environment="env" @publishTriggered="publishEnvironment"/>
      </div>
    </transition-group>

    <div class="deployment-page__new" @click="newEnvironment()">
      <div>
        <img  src="../../../../Frontend/public/assets/newitem.png" />
        <label>New Environment</label>
      </div>
    </div>
  </div>
</template>

<script>
import DeployPage from './DeployPage.hook'
import EnvironmentCardMolecule from './EnvironmentCardMolecule/EnvironmentCardMolecule';
const hook = new DeployPage();
hook.setBackendNamespace("Pages/Editor").setElementName("DeployPage");

export default {
  name: 'DeployPage',
  beforeCreate() { hook.hook(this)},
  beforeDestroy(){ hook.clearToolBar();}, 
  mounted(){ hook.initToolbar(); },
  components:{
    EnvironmentCardMolecule
  },
  computed: hook.getters("openedDocument"),
  methods: hook.methods("newEnvironment","publishEnvironment"),
  props: { },
  data() {
      return { }
  }
}

</script>

<style scoped lang="scss">

.deployment-page {
  
  background-color: #1E1E1E;
  height:100%;
  width:100%;
  overflow: auto;
  

  &__environments {
    margin-top:30px;
    margin-left:30px;
    width:960px;
  }

  &__new{
    margin-top:30px;
    margin-left:30px;
    width:960px;

    height:100px;
    border-radius: 5px;
    background-color: #333333;
    
    display: flex;
    justify-content: center;
    
    cursor: pointer;

    div {
      margin:auto;
      display: flex;
      justify-content: center;
      cursor: pointer;

      img {
        height:40px;
        cursor: pointer;
      }
      label {
        cursor: pointer;
        font-size: 22px;
        margin-left:25px;
        margin-top:8px;
      }
    }

  }

  .list-item {
    display: inline-block;
    margin-right: 10px;
  }
  .list-enter-active, .list-leave-active {
    transition: all 0.5s;
  }
  .list-enter, .list-leave-to {
    opacity: 0;
  }

}

</style>
